export default function NewMedia(props) {
  const dispatch = useDispatch();
  const dataString = useSelector(selectTempData).dataString;
  const chartDataObject = useSelector(selectTempData).dataObject;

  const [activeEditor, setActiveEditor] = useState(null);

  const applicationState = useSelector(selectApplication);
  // const userData = useSelector(selectUserDataItems);

  const [savedOpenMediaURL, setSavedOpenMediaURL] = React.useState(false);

  function setFormObject(objectWithNamesValues) {
    console.log(objectWithNamesValues);
    if (applicationState.email.length > 6) {
      // let userEmail = applicationState.email.replace(/[^a-zA-Z0-9]/g, "_");

      let idPost = firebase.database().ref('openmedia').push().key;

      let dataObject = (() => {
        if (activeEditor === 'html' || activeEditor === 'markdown') { return { html: dataString } }

        if (activeEditor === "datetimebarapexchart") { return { datetimebarapexchart: chartDataObject } }
        if (activeEditor === "datetimelineapexchart") { return { datetimelineapexchart: chartDataObject } }
        if (activeEditor === "arraycategorieslineapexchart") { return { arraycategorieslineapexchart: chartDataObject } }
        if (activeEditor === "arraycategoriesbarapexchart") { return { arraycategoriesbarapexchart: chartDataObject } }

        return {};
      })();

      //       let dataObject = {
      //         requiredseries: props.reportIndicators,
      //         requiredcategories: props.categories,
      //         type,
      //         options
      //       }

      let postObject = {
        id: idPost,
        title:
          objectWithNamesValues.title.length > 2
            ? objectWithNamesValues.title
            : "Диаграмма",
        theme: objectWithNamesValues.theme,
        // answer: applicationState.currentProjectMediaAndDataAndTemplatesURL,
        comment: objectWithNamesValues.comment,
        type: "media", // activeEditor,
        content: idPost,
        // quizString: applicationState.currentProjectReportIndicatorsDictionary,
        deleted: false,
        email: applicationState.email,
        user: applicationState.user,
        avatarUrl: !!applicationState?.avatarUrl
          ? applicationState.avatarUrl
          : null,
        date: new Intl.DateTimeFormat("ru", {
          weekday: "short",
          year: "numeric",
          month: "short",
          day: "numeric",
          hour: "numeric",
          minute: "numeric",
        }).format(new Date()), //Date().toJSON()
      };

      console.log(postObject);



      // props.firebase.openmedia(idPost).set(postObject)
      //   .then(() => {
      //     props.firebase.openmediadata(idPost).set(dataObject).
      //       then(() => { setActiveEditor(false); setSavedOpenMediaURL(idPost); dispatch(emptyTempData()) })
      //       .catch(error => console.log(error))
      //   })
      //   .catch(error => console.log(error))

     
      let updates = {};
      updates["/openmedia/" + idPost] = postObject;
      updates["/openmediadata/" + idPost] = dataObject;
      console.log(updates);
      return firebase.database().ref().update(updates).then(() => {
        setActiveEditor(false); setSavedOpenMediaURL(idPost); dispatch(emptyTempData())
      });
    }
  }


  return (
    <Container>
      <ButtonToolbar className="m-3" aria-label="Toolbar with Button groups">
        <ButtonGroup className="mb-2" aria-label="Second group">
          <Button
            variant={
              activeEditor === "markdown" ? "secondary" : "outline-secondary"
            }
            onClick={() => setActiveEditor("markdown")}
          >
            Markdown
          </Button>{" "}
          <Button
            variant={
              activeEditor === "html" ? "secondary" : "outline-secondary"
            }
            onClick={() => setActiveEditor("html")}
          >
            HTML
          </Button>{" "}
          <Button
            variant={
              activeEditor === "datetimeareanocategoriesapexchart"
                ? "secondary"
                : "outline-secondary"
            }
            onClick={() => setActiveEditor("datetimeareanocategoriesapexchart")}
          >
            DateTimeAreaNoCategoriesApexChart
          </Button>
          <Button
            variant={
              activeEditor === "scatterapexchart"
                ? "secondary"
                : "outline-secondary"
            }
            onClick={() => setActiveEditor("scatterapexchart")}
          >
            ScatterApexChart
          </Button>
        </ButtonGroup>

        <ButtonGroup className="mb-2" aria-label="First group">
          <Button
            variant={
              activeEditor === "datetimebarapexchart"
                ? "secondary"
                : "outline-secondary"
            }
            onClick={() => setActiveEditor("datetimebarapexchart")}
          >
            DateTimeBarApexChart
          </Button>{" "}
          <Button
            variant={
              activeEditor === "datetimelineapexchart"
                ? "secondary"
                : "outline-secondary"
            }
            onClick={() => setActiveEditor("datetimelineapexchart")}
          >
            DateTimeLineApexChart
          </Button>
          <Button
            variant={
              activeEditor === "arraycategorieslineapexchart"
                ? "secondary"
                : "outline-secondary"
            }
            onClick={() => setActiveEditor("arraycategorieslineapexchart")}
          >
            ArrayCategoriesLineApexChart
          </Button>
          <Button
            variant={
              activeEditor === "arraycategoriesbarapexchart"
                ? "secondary"
                : "outline-secondary"
            }
            onClick={() => setActiveEditor("arraycategoriesbarapexchart")}
          >
            ArrayCategoriesBarApexChart
          </Button>
        </ButtonGroup>
      </ButtonToolbar>

      {!activeEditor && !!savedOpenMediaURL && <Alert variant='success'>
        <p>Сохранено</p>
        <Alert.Link href={"econolabs.github.io/openmedia/?" + savedOpenMediaURL}>Проверить</Alert.Link> {/* https://fincalculations.firebaseapp.com/#/emptyspreadsheet/ */}
      </Alert>}


      {!!activeEditor && <PostFormHook setFormObject={setFormObject} />}

      {activeEditor === "html" && <HTMLEditor showAs={activeEditor} />}

      {activeEditor === "markdown" && (
        <MarkdownEditorBlock showAs={activeEditor} />
      )}

      {activeEditor === "datetimebarapexchart" && (
        <DateTimeApexChartEditor showAs={activeEditor} />
      )}
      {activeEditor === "datetimelineapexchart" && (
        <DateTimeApexChartEditor showAs={activeEditor} />
      )}

      {activeEditor === "datetimeareanocategoriesapexchart" && (
        <DateTimeAreaNoCategoriesApexChartEditor showAs={activeEditor} />
      )}

      {activeEditor === "arraycategorieslineapexchart" && (
        <ArrayCategoriesApexChartEditor showAs={activeEditor} />
      )}
      {activeEditor === "arraycategoriesbarapexchart" && (
        <ArrayCategoriesApexChartEditor showAs={activeEditor} />
      )}

      {activeEditor === "scatterapexchart" && (
        <ScatterApexChartEditor showAs={activeEditor} />
      )}
    </Container>
  );
}

