function doCreateProtoArrayFromChartSeries(series) {
  let newProtoArray = [];
  for (const element of series[0].data) {
    newProtoArray.push([element[0], element[1]]);
  }
  return newProtoArray;
}

function doCreateChartSeriesFromSpreadsheet(data) {
  console.log(data);
  let seriesData = [];
  for (const element of data) {
    seriesData.push([element[0], element[1]]);
  }
  return seriesData;
}

function EditMediaTableNoCategories(props) {
  const dispatch = useDispatch();
  const data = useSelector(selectSpreadsheetData);

  useEffect(() => {
    dispatch(
      load_data({ protoData: doCreateProtoArrayFromChartSeries(props.series) })
    );
    dispatch(set_spreadsheetTitle({ spreadsheetTitle: "Chart Data" }));
  }, []);

  if (props.series.length === 0) {
    return <div>...</div>;
  }

  function doUpdateChart() {
    //   console.log(docreateChartDataFromSpreadsheet(data));
    props.doUpdateChartSeries([
      { name: "Данные", data: doCreateChartSeriesFromSpreadsheet(data) },
    ]);
  }

  return (
    <Container>
      <SpreadsheetLayout />
      <Button
        variant={"outline-secondary"}
        className="m-3"
        onClick={() => doUpdateChart()}
      >
        Обновить Not active yet
      </Button>{" "}
    </Container>
  );
}

function DateTimeApexChartEditor({ showAs }) {
  const [series, setSeries] = useState([
    { name: "Компания A", data: [100, 1500, 1150, 3000] },
    { name: "Компания Б", data: [1000, 900, 1460, 1300] },
    { name: "Компания В", data: [1000, 1300, 1250, 1550] },
    { name: "Индекс", data: [1000, 1400, 1200, 1600] },
  ]);

  const [categories, setCategories] = useState(
    [...new Array(4)].map((item, index) => {
      var d = new Date();
      return d.setMonth(d.getMonth() - 4 + index);
    })
  ); //

  function doUpdateChartCategoriesSeries(object) {
    console.log(object);
    //  console.log(object.series);
    setSeries(object.series);
    setCategories(object.categories);
  }

  return (
    <Container className="mt-3">
      <EditMediaTable
        series={series}
        categories={categories}
        doUpdateChartCategoriesSeries={doUpdateChartCategoriesSeries}
        showAs={showAs}
      />
      <ApexEmptyOptionsChart
        series={series}
        categories={categories}
        showAs={showAs}
      />
    </Container>
  );
}

