function docreateProtoArrayFromChartCategoriesSeries(categories, series) {
  let newProtoArray = [];
  let firstRow = ["Показатели"];
  for (const element of categories) {
    firstRow.push(element);
  }
  newProtoArray[0] = firstRow;

  for (let element of series) {
    let newRow = [element.name, ...element.data];
    newProtoArray.push(newRow);
  }
  return newProtoArray;
}

function docreateChartDataFromSpreadsheet(data) {
  let categories = [];
  for (const element of data[0].slice(1)) {
    categories.push(element);
  }

  let series = [];
  for (const element of data.slice(1)) {
    let newSerie = { name: element[0], data: element.slice(1) };
    series.push(newSerie);
  }

  return { categories, series };
}

function EditMediaTable(props) {
  const dispatch = useDispatch();
  const data = useSelector(selectSpreadsheetData);

  useEffect(() => {
    dispatch(
      load_data({
        protoData: docreateProtoArrayFromChartCategoriesSeries(
          props.categories,
          props.series
        ),
      })
    );
    dispatch(set_spreadsheetTitle({ spreadsheetTitle: "Chart Data" }));
  }, []);

  if (props.series.length === 0 || props.categories.length === 0) {
    return <div>...</div>;
  }

  function doUpdateChart() {

    let { categories, series } = docreateChartDataFromSpreadsheet(data);

    let options = (() => {
      if (props.showAs === "datetimebarapexchart") {
        return {
          chart: { type: 'bar', toolbar: { show: false } },
          title: { text: "", align: "left", style: { fontFamily: "Helvetica, Arial, sans-serif", marginTop: "2rem", fontWeight: 'normal' } },
          plotOptions: { bar: { horizontal: false, columnWidth: '55%' } }, // columnWidth: '55%', endingShape: 'rounded'
          dataLabels: { enabled: false },
          stroke: { show: true, width: 1, colors: ['transparent'] },
          xaxis: {
            type: "datetime", // categories: makeCategories(categories),
            title: { text: "Дата", style: { fontFamily: "Helvetica, Arial, sans-serif", margin: "1rem", padding: "1rem", fontWeight: 'normal' } }
          },
          yaxis: { title: { text: "Значение" }, style: { fontSize: '1rem', fontFamily: 'Helvetica, Arial, sans-serif', fontWeight: 'normal', cssClass: 'apexcharts-yaxis-title' } },
          fill: { opacity: 1 },
          tooltip: {
            x: { format: "MMM yy" }, //'20' + 'yy'
            //        y: { formatter: function (val) { return val } }
          },
        }
      };

      if (props.showAs === "datetimelineapexchart") {
        return {
          chart: { type: "line", toolbar: { show: false } },
          title: { text: "", align: "left", style: { fontFamily: "Helvetica, Arial, sans-serif", marginTop: "2rem", fontWeight: 'normal' } },
          stroke: { curve: "smooth" },
          xaxis: {
            type: "datetime",
            //  categories: makeCategories(categories),
            title: { text: "Дата", style: { fontFamily: "Helvetica, Arial, sans-serif", margin: "1rem", padding: "1rem", fontWeight: 'normal' } },
            tooltip: { x: { format: "dd MMM yyyy" } }
          },
          markers: { size: 2, colors: ["#D3D3D3"], strokeColors: "#808080", strokeWidth: 2, hover: { size: 4 } },
          yaxis: {
            title: {
              text: "Значение",
              style: { fontSize: '1rem', fontFamily: 'Helvetica, Arial, sans-serif', fontWeight: 'normal', cssClass: 'apexcharts-yaxis-title' }
            },
            tickAmount: 5,
            //        labels: { formatter: function (val) { return parseFloat(val).toFixed(1) } },
            // min: 800, max: 1800,
          },
        }
      }

      if (props.showAs === "arraycategorieslineapexchart") {
        return {
          chart: { type: 'line', toolbar: { show: false } },
          title: { text: "", align: "left", style: { fontFamily: "Helvetica, Arial, sans-serif", marginTop: "2rem", fontWeight: 'normal' } },
          // stroke: { width: 7, curve: 'smooth' },
          dataLabels: { enabled: true },
          xaxis: {
            categories: categories,
            title: { text: "", style: { fontFamily: "Helvetica, Arial, sans-serif", margin: "1rem", padding: "1rem", fontWeight: 'normal', cssClass: 'apexcharts-xaxis-title' } }
          },
          markers: { size: 2, colors: ["#D3D3D3"], strokeColors: "#808080", strokeWidth: 2, hover: { size: 4 } },
          yaxis: {
            title: {
              text: "Значение",
              style: { fontSize: '1rem', fontFamily: 'Helvetica, Arial, sans-serif', fontWeight: 'normal', cssClass: 'apexcharts-yaxis-title' }
            },
            //  tickAmount: 8,
            //  labels: { formatter: function (val) { return parseFloat(val).toFixed(1) } }
            // min: -20,
            // max: 20,
          }
        }
      }

      if (props.showAs === "arraycategoriesbarapexchart") {
        return {
          chart: { type: 'bar', toolbar: { show: false } },
          title: { text: "", align: "left", style: { fontFamily: "Helvetica, Arial, sans-serif", marginTop: "2rem", fontWeight: 'normal' } },
          // stroke: { width: 7, curve: 'smooth' },
          dataLabels: { enabled: true },
          xaxis: {
            categories: categories,
            title: { text: "", style: { fontFamily: "Helvetica, Arial, sans-serif", margin: "1rem", padding: "1rem", fontWeight: 'normal', cssClass: 'apexcharts-xaxis-title' } }
          },
          markers: { size: 2, colors: ["#D3D3D3"], strokeColors: "#808080", strokeWidth: 2, hover: { size: 4 } },
          yaxis: {
            title: {
              text: "Значение",
              style: { fontSize: '1rem', fontFamily: 'Helvetica, Arial, sans-serif', fontWeight: 'normal', cssClass: 'apexcharts-yaxis-title' }
            },
            //  tickAmount: 8,
            //  labels: { formatter: function (val) { return parseFloat(val).toFixed(1) } }
            // min: -20,
            // max: 20,
          }
        }
      }
      return {};
    })();


    props.doUpdateChartCategoriesSeries({ categories, series });
    dispatch(setTempDataObject({ dataObject: { categories, series, options } }));
  }

  return (
    <Container>
      <SpreadsheetLayout />
      <Button
        variant={"outline-secondary"}
        className="m-3"
        onClick={() => doUpdateChart()}
      >
        Обновить
      </Button>{" "}
    </Container>
  );
}

let modalForm = [
  { name: "theme", title: "Тема", type: "text" },
  { name: "title", title: "Название", type: "text" },
  { name: "comment", title: "Комментарий", type: "text" },
];

function PostFormHook(props) {
  const { register, handleSubmit, reset } = useForm({
    //, watch, errors
    defaultValues: { title: props?.title },
  });

  React.useEffect(() => {
    reset({ title: props?.title });
  }, [props?.title]);

  return (
    <form onSubmit={handleSubmit(props.setFormObject)}>
      {modalForm.map((item) => (
        <InputGroup className="mb-3" size="sm" key={item.name}>
          <InputGroup.Prepend>
            <InputGroup.Text style={{ width: "8rem" }}>
              {item.title}
            </InputGroup.Text>
          </InputGroup.Prepend>
          <FormControl
            key={item.name}
            name={item.name}
            type={item.type}
            ref={register}
          //       as={item.type === "textarea" ? "textarea" : "input"}
          />
        </InputGroup>
      ))}
      <Button variant="outline-secondary" type="submit" size="sm">
        Сохранить
      </Button>
    </form>
  );
}
